.card{
    display: flex;
    align-items: center;
    padding: 38px;
    gap: 20px;
    border-bottom: 1px solid #F5F5F5;

    
}
.locate{
    font-family: "Inter";
    display: flex;
    align-items: center;
    gap: 8px;
}
.locate img{
    width: 8px;
    height: 11px;
}
.locate p{
    
    font-size: 12px;
    letter-spacing: .17em;
}
.locate a{
    
    font-size: 14px;
}
.card h1{
    margin: 0;
}
.card-right p:nth-last-child(2){
    font-size: 10.24px;
    font-weight: bold;
}
.card-right p:nth-last-child(1){
    font-size: 10.24;
    line-height: 150%;
}